.header-div {
	height: 10vh;
	background-color: #282c34;
	display: flex;
	flex-direction: row;
	align-items: center;
	/* justify-content: center; */
	font-size: calc(10px + 3vmin);
	color: white;
}

.header-img > img {
	margin-left: 1vw;
	margin-right: 1vw;
	height: 7vh;
	flex-flow: left;
}

.header-link {
	flex: 1;
	text-align: right;
	margin-right: 2vw;
}

.header-link > .link {
	color: #61dafb;
}
